.controlBoardContainer {
    background-color: #202122;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-self: baseline;
    border-radius: 10px;
    width: 20vw;
}

.controlBoardItem {
    margin-left: 1rem;
    margin-right: 1rem;
    padding: 1rem 0 1rem 0;
}

.controlBoardUser {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 1rem;
}

.controlBoardName {
    font-size: large;
    color: white;
    margin-left: 1rem;
}

.controlBoardItemContainer {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 1rem;
}

.link {
    text-decoration: none;
}

.controlBoardLogo {
    width: 2rem;    
    flex-shrink: 0;
    align-self: center;
}

@media (max-width: 768px) {
    .controlBoardContainer {
        display: none  ;
    }
}